<template>
        <div class="audioContainer">
            <a class="nav-icon" v-on:click="isPlaylistActive=!isPlaylistActive" :class="{'isActive': isPlaylistActive}" title="Music List">
              <span></span>
              <span></span>
              <span></span>
            </a>
            <div class="audioPlayerList" :class="{'isActive': isPlaylistActive}">
                <div class="item" v-for="(item,index) in musicPlaylist" v-bind:class="{ 'isActive':isCurrentSong(index) }" v-on:click="changeSong(index),isPlaylistActive=!isPlaylistActive">
                    <p class="title">{{ item.title }}</p>
                    <p class="artist">{{ item.artist }}</p>
                </div>
    
            </div>
            <div class="audioPlayerUI" :class="{'isDisabled': isPlaylistActive}">
                <div class="albumImage">
                    <transition name="ballmove" enter-active-class="animated zoomIn" leave-active-class="animated fadeOutDown" mode="out-in">
                        <!--<transition name="slide-fade" mode="out-in">-->
                        <img @load="onImageLoaded()" :src="musicPlaylist[currentSong].image" :key="currentSong" ondragstart="return false;" id="playerAlbumArt">
                    </transition>
                    <div class="loader" :key="currentSong">Loading...</div>
                </div>

    
                <div class="playerButtons">
                    <a class="button" :class="{'isDisabled':(currentSong==0)}" v-on:click="prevSong()" title="Previous Song"><i class="zmdi zmdi-skip-previous"></i></a>
                    <a class="button play" v-on:click="playAudio()" title="Play/Pause Song">
                        <transition name="slide-fade" mode="out-in">
                            <i class="zmdi" :class="[currentlyStopped ? 'zmdi-stop' : (currentlyPlaying ? 'zmdi-pause-circle' : 'zmdi-play-circle')]" :key="1"></i>
                        </transition>
                    </a>
                    <a class="button" :class="{'isDisabled':(currentSong==musicPlaylist.length-1)}" v-on:click="nextSong()" title="Next Song"><i class="zmdi zmdi-skip-next"></i></a>
                </div>
    
                <div class="currentTimeContainer" style="text-align:center">
                    <span class="currentTime">{{ currentTime | fancyTimeFormat }}</span>
                    <span class="totalTime"> {{ trackDuration | fancyTimeFormat }}</span>
                    <!--<span style="color:black">({{ currentSong+1 }}/{{ musicPlaylist.length }})</span>-->
                </div>
    
                <div class="currentProgressBar">
                    <div class="currentProgress" :style="{ width: currentProgressBar + '%' }"></div>
                </div>
            </div>
    
        </div>
    
</template>

<script>
export default {
    name: 'haha',
    data() {
        return {
            audio: "",
            imgLoaded: false,
            currentlyPlaying: false,
            currentlyStopped: false,
            currentTime: 0,
            checkingCurrentPositionInTrack: "",
            trackDuration: 0,
            currentProgressBar: 0,
            isPlaylistActive: false,
			currentSong: 0,
			money: 10000,
            debug: false,
            musicPlaylist: [
                {
                    title: "밤편지",
                    artist: "아이유",
                    url: "https://ipfs.io/ipfs/QmXGqnBZ5F1bMHbRahtfWVKFcZZHxqXCJnfV7zcqe16xm1",
                    image: "https://i1.sndcdn.com/artworks-000214603672-eq8aj7-t500x500.jpg"
                },
                {
                    title: "음양사",
                    artist: "아이유",
                    url: "https://ipfs.io/ipfs/QmVXvuMUzAo4eV7JvYH9Vnvm4MZHsq2Do516F8KbGf15Qz",
                    image: "https://i1.sndcdn.com/artworks-000237042876-9z3mvx-t500x500.jpg"
                },
                {
                    title: "별",
                    artist: "유성은 & 로꼬",
                    url: "https://ipfs.io/ipfs/QmbL8ivwA7b66bqP14cuJKtFwYVj7Zvgq8gbJsUUCvrD1L",
                    image: "https://i1.sndcdn.com/artworks-000455308941-zbf0c5-t500x500.jpg"
                },
                {
                    title: "첫눈에",
                    artist: "헤이즈",
                    url: "https://ipfs.io/ipfs/QmV45NCzdeDXCDwMV22ZnhGWYfP3uQfAzAwig2y5nB412W",
                    image: "https://i1.sndcdn.com/artworks-000458261289-28j6qv-t500x500.jpg"
				},
				{
					title: "April come she will",
                    artist: "simon",
                    url: "https://ipfs.io/ipfs/QmNn8PkPYE274HikGpEFXUQWPmwu8biUHicAxBa65ipfE6",
                    image: "https://m.media-amazon.com/images/M/MV5BNTMyZDdiMzUtZjcxNS00Mjc3LTljY2UtYjI4YmY5NzJlYjc1XkEyXkFqcGdeQXVyMTA5OTkwNTc@._V1_UX182_CR0,0,182,268_AL_.jpg"

				},
				{
					title: "Smooth Criminal",
					artist: "Jackson",
					url : "https://ipfs.io/ipfs/QmTqunj9qH1jYpkWyoS3YeJpXLrmwjeXEtkJgJNZHTxABK",        // sweetbox 임 사실 
					image: "https://m.media-amazon.com/images/M/MV5BMzIwNmU2NWItNjhjZi00MDIxLWFlN2UtODlkMzIzMjUzZDZiXkEyXkFqcGdeQXVyNTk1NTMyNzM@._V1_SX1777_CR0,0,1777,985_AL_.jpg"
				},
				{
					title: "butterfly",
					artist: "러브홀릭",
					url : "https://ipfs.io/ipfs/QmWXusT18o9qCfZij9i62VygQDp4X3PLNu2Ek3R9zL9VrT",
					image : "https://t1.daumcdn.net/cfile/tistory/235C963F55CB4DE727"
				}


			],
            audioFile: ""
        }
    },
    mounted: function() {
		this.changeSong();
		this.audio.loop = false;
	},
	filters: {
		fancyTimeFormat: function(s) {
			return (s - (s %= 60)) / 60 + (9 < s ? ":" : ":0") + s;
		}
	},
	methods: {
		togglePlaylist: function() {
			this.isPlaylistActive = !this.isPlaylistActive;
		},
		nextSong: function() {
			if (this.currentSong < this.musicPlaylist.length - 1)
				this.changeSong(this.currentSong + 1);
		},
		prevSong: function() {
			if (this.currentSong > 0) this.changeSong(this.currentSong - 1);
		},
		changeSong: function(index) {
			var wasPlaying = this.currentlyPlaying;
			this.imageLoaded = false;
			if (index !== undefined) {
				this.stopAudio();
				this.currentSong = index;
			}
			this.audioFile = this.musicPlaylist[this.currentSong].url;
			this.audio = new Audio(this.audioFile);
			var localThis = this;
			this.audio.addEventListener("loadedmetadata", function() {
				localThis.trackDuration = Math.round(this.duration);
			});
			this.audio.addEventListener("ended", this.handleEnded);
			if (wasPlaying) {
				this.playAudio();
			}
		},
		isCurrentSong: function(index) {
			if (this.currentSong == index) {
				return true;
			}
			return false;
		},
		getCurrentSong: function(currentSong) {
			return this.musicPlaylist[currentSong].url;
		},
		playAudio: function() {
			if (
				this.currentlyStopped == true &&
				this.currentSong + 1 == this.musicPlaylist.length
			) {
				this.currentSong = 0;
				this.changeSong();
			}
			if (!this.currentlyPlaying) {
				this.getCurrentTimeEverySecond(true);
				this.currentlyPlaying = true;
				this.audio.play();
			} else {
				this.stopAudio();
			}
			this.currentlyStopped = false;
		},
		stopAudio: function() {
			this.audio.pause();
			this.currentlyPlaying = false;
			this.pausedMusic();
		},
		handleEnded: function() {
			if (this.currentSong + 1 == this.musicPlaylist.length) {
				this.stopAudio();
				this.currentlyPlaying = false;
				this.currentlyStopped = true;
			} else {
				this.currentlyPlaying = false;
				this.currentSong++;
				this.changeSong();
				this.playAudio();
			}
		},
		onImageLoaded: function() {
			this.imgLoaded = true;
		},
		getCurrentTimeEverySecond: function(startStop) {
			var localThis = this;
			this.checkingCurrentPositionInTrack = setTimeout(
				function() {
					localThis.currentTime = localThis.audio.currentTime;
					localThis.currentProgressBar =
						localThis.audio.currentTime / localThis.trackDuration * 100;
					localThis.getCurrentTimeEverySecond(true);
				}.bind(this),
				1000
			);
		},
		pausedMusic: function() {
			clearTimeout(this.checkingCurrentPositionInTrack);
		}
	},
	watch: {
		currentTime: function() {
			this.currentTime = Math.round(this.currentTime);
		}
	},
	beforeDestroy: function() {
		this.audio.removeEventListener("ended", this.handleEnded);
		this.audio.removeEventListener("loadedmetadata", this.handleEnded);

		clearTimeout(this.checkingCurrentPositionInTrack);
	}
}
</script>


